<template>
  <div id="store-details">
    <loading-big v-show="loading"/>
    <div class="store-details-container" v-if="store">
      <div class="store-details-cover">
        <img :src="store.photoUrl" :alt="store.name">
      </div>
      <div class="store-details-body">
        <div class="store-details-content">
          <div class="logo-part-wrapper">
            <div class="logo-container">
              <img :src="store.logoUrl" :alt="store.name[locale]">
            </div>
            <div class="subscribe-button-container">
              <button class="subscribe-button" :class="{alreadySubscribed: isSubscribed}" @click="subscribe(store._id)">
                <span class="subscribe" v-if="!isSubscribed">
                  {{t('subscribe')}}
                </span>
                <span class="unsubscribe" v-else>
                  <span class="subscribed">
                     <img src="../../../assets/images/icons/pwichka.svg" height="6" width="9"> <span>{{t('subscribed')}}</span>
                  </span>
                  <span class="hover">
                    {{t('unsubscribe')}}
                  </span>
                </span>
              </button>
            </div>
          </div>
          <div class="content-wrapper">
            <div class="content">
              <div class="content-main">
                <div class="breadcrumb">
                  <div class="breadcrumb-item">
                    <router-link :to="{name: 'home', params:{locale: locale}}">{{t('home')}}
                    </router-link>
                  </div>
                  <div class="breadcrumb-item">
                    <router-link :to="{name: 'stores'}">{{t('stores')}}</router-link>
                  </div>
                  <div class="breadcrumb-item">
                    <router-link to=""> {{store.name[locale]}}</router-link>
                  </div>
                </div>
                <div class="title-container">
                  <h1 class="title">{{store.name[locale]}}</h1>
                </div>
                <div class="p-container">
                  <p v-html="formatP(store.description[locale])"></p>
                </div>
              </div>
              <div class="content-socials">
                <div class="socials-wrapper">
                  <div class="socials-title">
                    <h4>{{t('socials')}}</h4>
                  </div>
                  <ul>
                    <li v-if="store.socials.facebook.length">
                      <a :href="store.socials.facebook" target="_blank">
                        <img src="../../../assets/images/icons/facebook.svg" height="15">
                      </a>
                    </li>
                    <li v-if="store.socials.twitter.length">
                      <a :href="store.socials.twitter" target="_blank">
                        <img src="../../../assets/images/icons/twitter.svg" height="12">
                      </a>
                    </li>
                    <li v-if="store.socials.youtube.length">
                      <a :href="store.socials.youtube" target="_blank">
                        <img src="../../../assets/images/icons/youtube.svg" height="10">
                      </a>
                    </li>
                    <li v-if="store.socials.pinterest.length">
                      <a :href="store.socials.pinterest" target="_blank">
                        <img src="../../../assets/images/icons/pinterest.svg" height="15">
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <div class="contact-info">
              <ul>
                <li>
                  <span class="icon store-phone">
                    <img src="../../../assets/images/icons/Phone.svg" alt="Phone icon">
                  </span>
                  <span class="info">
                    {{formatPhoneNumber(store.phone)}}
                  </span>
                </li>
                <li>
                  <span class="icon store-link">
                    <img src="../../../assets/images/icons/Web.svg" alt="Web icon">
                  </span>
                  <span class="info">
                    <a :href="formatUrl(store.websiteLink)" target="_blank">
                      {{store.websiteLink}}
                    </a>
                  </span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="share-container">
        <social-sharing :url="currentFullUrl"
                        class="share-inner"
                        :title="store.name[locale]"
                        :description="store.description[locale]"
                        :hashtags="formatFilters"
                        inline-template>
          <div>
            <network network="facebook">
              <button class="share-button">
                <span class="icon-container">
                  <font-awesome-icon :icon="{ prefix: 'fab', iconName: 'facebook-f' }"/>
                </span>
                <span>{{t('share_on_facebook')}}</span>
              </button>
            </network>
            <network network="twitter">
              <button class="share-button">
                <span class="icon-container">
                  <font-awesome-icon :icon="{ prefix: 'fab', iconName: 'twitter' }"/>
                </span>
                <span>{{t('share_on_twitter')}}</span>
              </button>
            </network>
            <network network="linkedin">
              <button class="share-button">
                <span class="icon-container">
                  <font-awesome-icon :icon="{ prefix: 'fab', iconName: 'linkedin-in' }"/>
                </span>
                <span>{{t('share_on_linkedin')}}</span>
              </button>
            </network>
          </div>
        </social-sharing>
      </div>
    </div>
  </div>
</template>
<script>
import LoadingBig from '../../partials/LoadingBig'
import SocialSharingNetwork from 'vue-social-sharing/src/social-sharing-network'
import ButtonStandard from '../../partials/StandardButton'

export default {
  name: 'store-details',
  components: { ButtonStandard, SocialSharingNetwork, LoadingBig },
  mounted: function () {
    this.loadStore()
    if (this.$store.getters.user && !this.$store.getters.subscribed.length) {
      this.$store.dispatch('getSubscribed').catch((error) => {
        console.error(error)
      })
    }
  },
  data: function () {
    return {
      store: null,
      loading: false
    }
  },
  computed: {
    isSubscribed: function () {
      if (!this.$store.getters.user) {
        return false
      }
      const store = this.$store.getters.subscribed.filter(object => {
        if (object && object._id === this.store._id) {
          return object
        }
      })
      return store.length
    },
    formatFilters: function () {
      return this.store.filters.join(',')
    }
  },
  methods: {
    loadStore: function () {
      this.loading = true
      this.$store.dispatch('findStore', this.$route.params.store).then((response) => {
        this.store = response
        this.loading = false
      })
    },
    subscribe: function (id) {
      const user = this.$store.getters.user
      if (!user) {
        this.$router.push({ name: 'login' })
      } else {
        this.$http.post(this.$store.state.apiUrls.subscribe, {
          userToken: user.token,
          storeId: id
        }).then(() => {
          this.$notify({
            group: 'notify',
            type: 'success',
            title: this.t('subscribed_successfully')
          })
          this.$store.dispatch('getSubscribed').catch((error) => {
            console.error(error)
          })
        }).catch((error) => {
          console.error(error)
          this.$notify({
            group: 'notify',
            type: 'error',
            title: 'Error',
            text: error
          })
        })
      }
    },
    getFacebookIcon: function () {
      return require('../../../assets/images/icons/facebook.svg')
    }
  }
}
</script>
<style lang="scss">
.breadcrumb {
  display: flex;
  .breadcrumb-item {
    font-size: 1.1rem;
    line-height: 1.27;
    color: #848484;
    &:nth-child(n + 2) {
      &:before {
        content: '>';
        display: inline-block;
        margin: 0 5px;
      }
    }
  }
}

.share-container {
  .share-inner {
    display: flex;
    justify-content: center;
    padding: 25px 0;
    border-top: 1px solid #dcdcdc;
    .share-button {
      border: 1px solid #dcdcdc;
      display: flex;
      background: transparent;
      width: 293px;
      margin: 0 5px;
      padding: 0;
      cursor: pointer;
      position: relative;
      &:before {
        content: '';
        height: 0;
        width: 100%;
        left: 0;
        bottom: 0;
        position: absolute;
        background: #000;
        z-index: 0;
        transition: height .3s;
      }
      &:hover {
        &:before {
          height: 100%;
        }
        span {
          &:first-child {
            svg {
              filter: invert(1);
            }
          }
          &:last-child {
            color: #ffffff;
          }
        }
      }
      span {
        margin: auto 0;
        z-index: 1;
        &:first-child {
          width: 46px;
          padding: 15px 0;
          border-right: 1px solid #dcdcdc;
          font-size: 1.5rem;
        }
        &:last-child {
          width: 100%;
          margin: auto;
          font-family: 'Muli SemiBold', 'BPG Nino Mtavruli', 'sans-serif';
          font-size: 12px;
          line-height: 1.25;
          text-transform: uppercase;
        }
      }
    }
  }
}

#store-details {
  .store-details-cover {
    width: 100%;
    padding-top: 39.584%;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  .store-details-body {
    width: 100%;
    max-width: 1640px;
    margin: 0 auto;
    @media screen and (max-width: 1660px){
      width: calc(100% - 40px);
      margin: 0 20px;
    }
    .store-details-content {
      display: flex;
      margin-top: 21px;
      .logo-part-wrapper {
        margin-right: 52px;
        .logo-container {
          height: 244px;
          width: 244px;
          background: white;
          display: flex;
          border: 1px solid rgba(0, 0, 0, 0.5);
          img {
            height: calc(100% - 100px);
            width:  calc(100% - 100px);
            margin: auto;
            object-fit: contain;
          }
        }
        .subscribe-button-container {
          width: 100%;
          margin-top: 10px;
          .subscribe-button {
            border: 1px solid #eead16;
            font-family: 'Muli', 'BPG Arial', 'sans-serif';
            width: 100%;
            padding: 12px 0 16px 0;
            background: transparent;
            font-size: 1.3rem;
            text-transform: capitalize;
            line-height: 1.23;
            text-align: center;
            color: #eead16;
            cursor: pointer;
            position: relative;
            &.alreadySubscribed{
              border: 1px solid #000000;
              color: #000000;
            }
            .hover{
              display: none;
            }
            .subscribed{
              img{
                margin-right: 5px;
              }
            }
            &:hover {
              .subscribed {
                display: none;
              }
              .hover {
                display: block;
              }
            }
            &:before {
              content: '';
              position: absolute;
              height: 0;
              width: 100%;
              background: #eead16;
              z-index: -1;
              bottom: 0;
              left: 0;
              transition: height 0.3s;
            }
            &.active,
            &:hover {
              color: #ffffff;
              &:before {
                height: 100%;
                transition: height 0.3s;
              }
            }
            &.alreadySubscribed{
              &:before {
                background: #000;
              }
            }
          }
        }
      }
      .content-wrapper {
        width: calc(100% - 296px);
        .content {
          display: flex;
          .content-main {
            width: 90%;
            max-width: 1050px;
            .title-container {
              .title {
                font-family: 'Muli SemiBold', 'BPG Nino Mtavruli', 'sans-serif';
                font-size: 3.5rem;
                margin: 15px 0 19px;
                font-weight: 600;
                line-height: 1.26;
                text-transform: uppercase;
              }
            }
            .p-container {
              p {
                font-family: 'Muli Light', 'BPG Arial', 'sans-serif';
                font-size: 1.8rem;
                font-weight: 300;
                line-height: 1.28;
                color: #000;
                opacity: 1;
              }
            }
          }
          .content-socials {
            width: 12%;
            max-width: 122px;
            display: flex;
            margin-left: auto;
            border-left: 1px solid #dcdcdc;
            border-right: 1px solid #dcdcdc;
            flex-direction: column;
            .socials-wrapper {
              margin: auto;
              .socials-title {
                h4 {
                  font-family: 'Muli Light', 'BPG Nino Mtavruli', 'sans-serif';
                  font-size: 1.4rem;
                  font-weight: 300;
                  text-transform: uppercase;
                  line-height: 1.29;
                  margin: 0;
                }
              }
              ul {
                li {
                  margin: 19px 0;
                  text-align: center;
                }
              }
            }
          }
        }
        .contact-info {
          margin-top: 21px;
          ul {
            width: 90%;
            max-width: 1050px;
            padding: 21px 0 17px;
            border-top: solid 1px #dcdcdc;
            display: flex;
            li {
              display: flex;
              margin-right: 54px;
              font-family: 'Muli Light', 'BPG Arial', 'sans-serif';
              font-size: 1.4rem;
              line-height: 1.29;
              > span {
                margin: auto 0;
              }
              .icon {
                height: 34px;
                width: 34px;
                margin-right: 25px;
                /*border: 1px solid #707070;*/
                /*border-radius: 50%;*/
                img {
                  height: 100%;
                  width: 100%;
                }
              }
              .info {
                color: #848484;
                a {
                  color: #2d83e6;
                  font-size: inherit;
                  line-height: inherit;
                  &:hover {
                    text-decoration: underline;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
